.Directory-container {
  padding: 0.5rem 0.5rem 2rem;
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 2;
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-color: #505050 #303030;
}

.Directory-container::-webkit-scrollbar {
  background-color: #303030;
  border-radius: 10px;
}

.Directory-container::-webkit-scrollbar-thumb {
  background: #505050;
  border-radius: 10px;
}

.Directory-regionSection > ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.Directory-regionSection > h2 {
  font-size: 1.25rem;
  font-style: italic;
}

.Directory-placeLink {
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  color: #a0ffc0;
  cursor: pointer;
}

.Directory-placeLink:hover {
  font-weight: 600;
  color: #80a0ff;
}

.Directory-typeIcon {
  margin-left: 0.35rem;
}

.Directory-type_abandoned {
  color: #ffa0b0;
}

.Directory-type_unsettled {
  color: #90b0ff;
}

.Directory-type_settled {
  color: white;
}